import './TweetBox.css'
import {Avatar, Button} from '@mui/material';
import React, {useState} from 'react'
import db from './firebase'

export default function TweetBox() {
  //skapa tv state som spr vad en anvndare skriver
  const [tweetMessage, setTweetMessage]=useState('');
  // fr spra bilden
  const [tweetImage, setTweetImage]=useState('');

  // eftersom vi r inne i en form s kommer den refresha siden per auto, i react vi vill inte refresha s vi skriver 
  //e.preventDefualt-> fr att frhindra refreshning av sidan
  const sendTweet= e=>{
    //Detta frhindra att sidan refresha 
    e.preventDefault();
    //shr postar vi i databasen 
    //vi adda en objekt 
    //Hr mappar vi in i objektet
    db.collection('posts').add({
      displayName: 'IsaacSkog',
      username: 'IsaacSkog',
      variefied: true,
      //hr skriver vi att meddelande ska bli det som vi skriver som i tweet o inte hrdkodat
      text: tweetMessage,
      image:tweetImage,
      avatar: "https://kajabi-storefronts-production.global.ssl.fastly.net/kajabi-storefronts-production/themes/284832/settings_images/rLlCifhXRJiT0RoN2FjK_Logo_roundbackground_black.png"
    });
    //Hr resetar vi fltet fr rensa dem 
    setTweetMessage('');
    setTweetImage('');
  };
  return (
    <div className='tweetBox'>
        <form>
            {/* enter funktion */}
            <div className='tweetBox__input'>
                <Avatar src= "https://kajabi-storefronts-production.global.ssl.fastly.net/kajabi-storefronts-production/themes/284832/settings_images/rLlCifhXRJiT0RoN2FjK_Logo_roundbackground_black.png"/>
                {/* hr vi mappar meddelanet */}
                <input 
                // fixa en map -> e=> nr ndras event nr stts meddelandet och ta vrde nr vi skriver
                onChange={e=>setTweetMessage(e.target.value)}
                value={tweetMessage} placeholder="Vad som hnder nu"  type='text'/>
            </div>
                <input 
                value={tweetImage}
                onChange={e=>setTweetImage(e.target.value)}
                className='tweetBox__imageInput' placeholder="Altenativ: Lgg till bild"  type='text'/> 
                {/* skapar en knapp d nr vi klickar p den s kommer skickas en tweet */}
            <Button onClick={sendTweet} type="submit" className='tweetBox__tweetButton'>Tweet</Button>
        </form>
    </div>
  )
}
