import React, { useState, useEffect } from 'react'
import './Feed.css';
import TweetBox from './TweetBox';
import Post from './Post'
import db from './firebase';
import FlipMove from 'react-flip-move';

export default function Feed() {
  // anvnder oss av state s skriver en variavel i react
  //variabelnam -> sen setpost o inilasera det som nt heter hook
  const [posts, setPosts] = useState([]);
  //fungerar nr vi ge vilkor 
  //kr nr feed komponent laddas vad som finns i koden o kr inte efter
  useEffect(()=>{
    //vran kollektion 
    db.collection("posts").onSnapshot((snapshot) =>
    setPosts(snapshot.docs.map((doc) => doc.data()))
  );
}, []);
  return (
    <div className='feed'>
        <div className='feed__header'>
        <h2>Home</h2>
        </div>
        <TweetBox/>
        {/* hr slr vi in mappen i flip */}
        {/* fr att flip ska fungerar vi behver en key */}
        <FlipMove>
        {posts.map((post)=>(
        <Post 
        // slnger vi key,idf pulling the dokument id frn db
        key={post.text}
        displayName={post.displayName}
        username={post.username}
        varified={post.verified}
        text={post.text}
        avatar={post.avatar}
        image={post.image}
        />
        ))}
        </FlipMove>
    </div>
  )
}
